.footer {
    color: #c7c7c7;
    font-size: 0.7em;
    margin-top: 2em;
}

.footer .links {
    display: flex;
    flex-wrap: wrap;
    margin: 0 0 2em 0;
    padding: 0;
    list-style: none;
}



.footer li:after {
    content: "\2022";
    margin: 0 0.25em 0 0.25em;
}

.footer li:last-child:after {
    content: "";
}